body {
    display: flex;
    align-items: center;
    justify-content: center;
    height:100vh;
    background-color: #fdf6b9;
  }
  
  .envelope {
    position: relative;
    cursor: pointer;
  }

  .back {
    position: relative;
    width:250px;
    height: 200px;
    background-color: #6247aa;
  }
  
  .letter {
    position: absolute;
    background-color: #fff;
    width:230px;
    height: 180px;
    top:10px;
    left:10px;
    transition: .2s;
  }
  
  .letter:before {
    position: absolute;
    content:"";
    
  }
  
  .letter:after {
    position: absolute;
    content:"";
   
  }
  
  .text {
    text-align: center;
    font-size: 8px;
    font-family: "Bradley Hand ITC";
    margin-top:20px;
    font-weight: bold;
  }
  
  .text:before, .text:after {
    content:"";
    position: absolute;
    
  }
  
  .text:before {
    left:108px;
    transform: rotate(-25deg);
  }
  
  .text:after {
    left:118px;
    transform: rotate(25deg);
  }
  
  .front {
    position: absolute;
    border-right: 130px solid #815ac0;
    border-top: 100px solid transparent;
    border-bottom: 100px solid transparent;
    height:0;
    width:0;
    top:0;
    left:120px;
    z-index:3;
  }
  
  .front:before {
    content:"";
    position: absolute;
    border-left: 130px solid #815ac0;
    border-top: 100px solid transparent;
    border-bottom: 100px solid transparent;
    height:0;
    width:0;
    top:-100px;
    left:-120px;
  }
  
  .front:after {
    content:"";
    position: absolute;
    border-bottom: 105px solid #7251b5;
    border-left: 125px solid transparent;
    border-right:125px solid transparent;
    height:0;
    width:0;
    top:-5px;
    left:-120px;
  }
  
  .top {
    position: absolute;
    border-top: 105px solid #9163cb;
    border-left: 125px solid transparent;
    border-right:125px solid transparent;
    height:0;
    width:0;
    top:0;
    transform-origin: top;
    transition: .4s;
  }
  .shadow {
    position: absolute;
    background-color: rgba(0,0,0,0.1);
    width:250px;
    height:10px;
    top:220px;
    border-radius:50%;
  }
  .envelope:hover .top {
    transform: rotateX(160deg);
  }
  
  .envelope:hover .letter {
    transform: translateY(-100px);
    z-index:2;
  }

  .msg2{
    margin-top: 50vh;
    font-size: 20px;
    font-family: "Bradley Hand ITC";
    font-weight: bold;
    text-shadow: 8px 4px 9px #f20044;
    position: absolute;
    font-weight: bold;
  }